<template>
    <div class="tasksContainer">
        <ul class="list">
            <li class="tasks" v-for="task in tasks" :key="task.id" v-bind:class="{ 'taskChecked': task.checked }">
                <font-awesome-icon icon="trash-alt" class="btnDeleteTask" v-on:click="handleDelete(task)"/>
                <font-awesome-icon icon="edit" class="btnEditTask" v-on:click="changeValueInput(task)"/>
                <template v-if="task.edit">
                    <div class="changeTask">
                        <input type="text" v-model="task.message" placeholder="changer le tâche">
                        <font-awesome-icon icon="check-square" class="btnValidateTask" v-on:click="handleChangeInput(task)"/>
                        <font-awesome-icon icon="window-close" class="btnCancelTask" v-on:click="handleCancelInput(task)"/>
                    </div>
                    
                </template>
                <template v-else>
                    <input type="checkbox" id="checkbox" v-model="task.checked" >
                    {{task.message}}
                </template>
            </li>
        </ul>
    </div>
</template>

<script>
import './Tasks.css'
export default {
    name: "Tasks",
    data() {
        return {
            currentTask: '',
            changeTasks: false,
        }
    },
    props: ['tasks'],
    methods: {
        handleDelete(task) {
            this.$emit('clickedDelete', task.message)
        },
        changeValueInput(task) {
            this.$emit('changeValueTask', task)
            this.currentTask = task
        },
        handleChangeInput(task) {
            console.log(task.message)
            this.$emit('handleChangeInput', task)
        },
        handleCancelInput(task) {
            this.$emit('handleCancelInput', task)
        }
    }
    
}
</script>
