<template>
    <div class="weather">
        <h1 class="title">App météo avec Vue.js</h1>
        <div class="group">
            <label for="position">Entrez le nom d'une ville</label>
            <input type="text" id="position" class="input" v-model="request" @keypress.enter="goWeather">
        </div>
        <div class="info" v-if="temps">
            <h3>Position : {{ temps.name }}</h3>
            <div class="card">
                <p class="text">Température : {{ temps.main.temp.toFixed() }}</p>
                <p class="text">Temps : {{ temps.weather[0].description }}</p>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import './Weather.css'
export default {
    name: 'Weather',
    data() {
        return {
            request: '',
            temps: undefined,
            api_code: '9bae57f8fc9a934af5668777315617bc',
            url: 'https://api.openweathermap.org/data/2.5/weather?'
        }
    },
    methods: {
        goWeather() {
                axios
                .get(`${this.url}q=${this.request}&units=metric&APPID=${this.api_code}&lang=fr`)
                .then(response => {
                    // console.log(response)
                    this.temps = response.data
                })
                this.request = ''
            }
    }
}
</script>