<template>
    <div class="container">

    <div class="todo">
        <h1 class="title">To-Do List</h1>
        <Input @clicked="onClickChild" />
        <Tasks v-bind:tasks="tasks" @clickedDelete="handleDelete" @changeValueTask="changeValueTask" @handleChangeInput="handleChangeInput" @handleCancelInput="handleCancelInput"/>
    </div>
    </div>
</template>

<script>
import './Todo.css'
import Input from '../Input/Input'
import Tasks from '../Tasks/Tasks'
export default {
    name: "Todo",
    data() {
        return {
            tasks : [
                {message: 'NuxtJS', edit: false, id: 1, checked: true},
                {message: 'Vue', edit: false, id: 2, checked: false},
                {message: 'React Native', edit: false, id: 3, checked: false}]
        }
        
    },
    components: {
        Input,
        Tasks
    },
    methods : {
           onClickChild (value) {
            const NewTask = {message: value, edit: false}
            this.tasks.push(NewTask)
      },
        handleDelete(value) {
            this.tasks = this.tasks.filter(task => task.message !== value);
        },
        changeValueTask(value) {
            for (const obj of this.tasks) {
                if (obj.message === value.message) {
                    obj.edit = !obj.edit
                }
            }
        },
        handleChangeInput(value) {
            for (const task of this.tasks) {
                if (task.id === value.id) {
                    task.message = value.message
                    task.edit = !task.edit
                }
            }
        },
        handleCancelInput(value) {
            for (const task of this.tasks) {
                if (task.id === value.id) {
                    task.edit = !task.edit
                    return task
                    
                }
            }
        }
    }

}
</script>
